<template>
   <div class="container">
        <div class="row">
          <div class="column" style="background-color:#e57373;">
            <h3>Share files      <br/>it's easy and secure</h3>
            <h6>Glide Share helps you to share your files through instantly shareable links.So that it can be downloaded easily.</h6>
            <img class="responsive-img img2" src="../assets/img2.png">
          </div>
          <div class="column" style="background-color:white;">
              <div class="s12">  
                <p class="flow-text">Welcome Back!</p>
              </div>
            <div class="row"> 
              <!-- <form> -->
                <div class="row">
                    <div class="file-field input-field">
                      <div class="btn">
                        <span>File</span>
                        <input type="file"
                        ref="file"
                        v-on:change="handleFileUpload()"
                        />
                      </div>
                      <div class="file-path-wrapper">
                        <input class="file-path validate" type="text" required>
                      </div>
                    </div>
                  <div class="input-field col s12">
                    Email:<input id="email" type="email" class="validate" v-model="email" required>
                  </div>
                </div>

                <button @click.prevent="submitFile()" class="btn waves-effect waves-light" type="submit" name="action">Share
                    <i class="material-icons right">send</i>
                </button>
              <!-- </form> -->
          </div>
          </div>
        </div>
      </div>
</template>

<script>
import axios from 'axios';
export default {
  name:"Index",
  
  data() {
    return {
      file: "",
      email: ""
    }
  },

  

  methods: {
      submitFile(){
            let formData = new FormData();
            formData.append('file', this.file);
            if(this.email != '' && this.file != '') {
                axios.post(`https://glide-share.herokuapp.com/${document.getElementById('email').value}`,
                formData,
                {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
              }
            ).then(() =>{
            console.log('SUCCESS!!');
             M.toast({html: 'Succuessfully sent!', classes: 'green'});
            this.email =''
            location.reload();
        })
        .catch(() => {
          console.log('FAILURE!!');
        });
      } else {
          M.toast({html: 'Please fill all the fields!', classes: 'black'});
      }

        },
        handleFileUpload(){
        this.file = this.$refs.file.files[0];
      }
      },
      
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Open+Sans&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
* {
  box-sizing: border-box;
}
.row {
      padding-top: 9%;
}
p{
  font-family: 'Josefin Sans', sans-serif;
  font-weight: bolder;
}
.flow-text {
    font-size: 2.68rem!important;
    padding-left:0px;
}
h3 {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: bolder;
}
h6{
  font-family: 'Nunito', sans-serif;  
  padding-left: 5px;
  font-weight:500!important;
  font-size: 1.1rem!important;
  color: black;
}
.l1 {
  padding-left: 130px;
}
label {
  font-size: 1.0rem!important;
}
.row .col.s12 {
  padding-top: 0px;
    top: 6px;
}
i.medium {
  padding-left: 150px!important;
}

/* Create two equal columns that floats next to each other */
.column {
  float: left;
  width: 50%;
  padding: 10px;
  height: 500px;
  border: none;
  border-radius: 8px;/* Should be removed. Only for demonstration */
} 
/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}
.s12 {
  padding-left: 47px;
}
.waves-effect {
  width: -webkit-fill-available;
}
.img2 {
  height:300px;
  width:340px;
  padding-top: 10%;
  padding-left: 25%;
}
.column {
  padding-top: initial;
}
.img1 {
  padding-left: 150px;
}
.btn {
  background-color: #e57373!important;
}
.btn-file {
    position: relative;
    overflow: hidden;
}
.btn-file input[type=file] {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 100%;
    min-height: 100%;
    font-size: 100px;
    text-align: right;
    filter: alpha(opacity=0);
    opacity: 0;
    outline: none;
    cursor: inherit;
    display: block;
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}
</style>



